@import url('https://fonts.googleapis.com/css?family=Lato+Sans');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Oswald:wght@200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

.Footer {
  font-family: 'Lato', sans-serif;
}

.footerContainer {
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background: linear-gradient(
      30.11deg,
      #ffffff -11.43%,
      #ffee93 13.8%,
      #f0d7bf 47.62%
    );
    opacity: 0.85;
    margin-top: 100px;
  }
  .iconsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .footerIcons {
    width: 50px;
    height: auto;
  }
  .centerContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  .nameContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .socialContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
  }
  .socialIconSmall {
    width: 30px;
    height: auto;
    padding: 5px;
  }
  .formContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 15px;
    border-radius: 10px;
    width: 250px;
  }
  .formInputWrapper {
    display: flex;
    flex-direction: column;
  }
  
  .inputBox {
    width: auto;
    height: 20px;
    margin: 5px;
    padding:10px 0 10px 5px;
  }
  button {
    width: auto;
    height: 25px;
    margin: 5px;
    border: 1px solid gray;
    border-radius: 5px;
  }
  
  @media screen and (max-width: 1200px) {
    .footerContainer {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap-reverse;
      justify-content: space-around;
      align-items: center;
      
    }
    .iconsContainer {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      margin-bottom: 10px;
    }
    .centerContainer {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
    }
    
  }

  .madeWith span:last-of-type {
    display: block;
  }
